import React from 'react';
import TabBarTabsNode from './TabBarTabsNode';
import TabBarNode from './TabBarNode';
import SaveRef from './SaveRef';
import { ITabsProps } from './utils';

export interface ITabBarProps extends ITabsProps {
  panes: React.ReactNode;
}

const TabBar: React.FC<ITabBarProps> = (props) => {
  return (
    <SaveRef>
      {(saveRef, getRef) => (
        <TabBarNode
          {...props}
          getRef={getRef}
          saveRef={saveRef}
        >
          <TabBarTabsNode
            saveRef={saveRef}
            {...props}
          />
        </TabBarNode>
      )}
    </SaveRef>
  );
};

export default TabBar;
